	export default {
		data() {
			return {
				key: '65bbf9c5a0ba3a024378876049a0fcc9',
				markes: '',
				origin: '',
				longitude: '',
				latitude: '',
				amapPlugin: null,
				covers: [{
					latitude: 39.9,
					longitude: 116.39,
					fillColor: '#FFC41F', //填充颜色
					color: '#12A1DD', //描边的颜色
					radius: 500, //半径
					strokeWidth: 2 //描边的宽度
				}]
			};
		},
		onLoad(data) {
			// this.markes = data.markes;
			this.covers[0].longitude = data.markes;
			this.covers[0].latitude = data.latitude;
			console.log(data);
			uni.getLocation({
				//获取当前的位置坐标
				type: 'gcj02',
				success: res => {
					this.longitude = res.longitude;
					this.latitude = res.latitude;
					this.origin =res.longitude+','+res.latitude
					this.amapPlugin = new amap.AMapWX({
						key: this.key
					});
					// this.getWalkingRoute();
					wx.openLocation({
					  latitude:res.latitude,
					  longitude: res.longitude,
					  scale: 18
					})
				}
			});
		},
		methods:{
			getWalkingRoute() {
				this.amapPlugin.getWalkingRoute({
					origin: this.origin,
					destination: this.markes,
					success: data => {
						console.log(data);
						console.log('成功了');
					},
					fail: (info) => {
						console.log(info);
						console.log('失败了');
					}
				})
			},
			tobtn() {
				
			}
		}
	}
